import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginformComponent } from './loginform/loginform.component';
import {MainTemplateComponent} from './main-template/main-template.component';
import {CoursesComponent} from './courses/courses.component';
import {CourseSignFormComponent} from './course-sign-form/course-sign-form.component';

const routes: Routes = [
  {path : 'login' , component : LoginformComponent},
  {path : 'courses' , component : CoursesComponent},
  {path : 'coursesign' , component : CourseSignFormComponent},
  {path : 'coursesign/:id' , component : CourseSignFormComponent},
  {path : '' , component : MainTemplateComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
