import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CoursesServiceService } from '../Services/courses-service.service';

@Component({
  selector: 'app-course-sign-form',
  templateUrl: './course-sign-form.component.html',
  styleUrls: ['./course-sign-form.component.css']
})
export class CourseSignFormComponent implements OnInit {
  public id;
  public courses;
  constructor(private Activatedroute: ActivatedRoute , private courseService: CoursesServiceService) { }

  ngOnInit() {
    this.courses = this.courseService.getCourses();
    this.id = this.Activatedroute.snapshot.paramMap.get('id');
  }

}
